import { newSpecPage } from '@stencil/core/testing';
import { EmailOverlayUI } from '../presto-email-overlay-ui';

describe('presto-email-overlay-ui', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [EmailOverlayUI],
      html: `<presto-email-overlay-ui></presto-email-overlay-ui>`,
    });
    expect(page.root).toEqualHtml(`
    <presto-email-overlay-ui>
       <mock:shadow-root>
         <div class="overlay" style="font-size: 8px;">
           <div class="content">
             <h1></h1>
             <form>
               <input required="" type="email">
               <button type="submit"></button>
             </form>
           </div>
         </div>
       </mock:shadow-root>
     </presto-email-overlay-ui>
    `);
  });
});
